.modalContainer {
  --light: hsl(0, 0%, 100%);
  --background: linear-gradient(to right bottom, hsl(140deg 6% 57%), hsl(184deg 31% 42%));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Open Sans', sans-serif;
  min-height: 100vh;
  background: linear-gradient(to bottom, hsl(236, 50%, 98%), hsl(236, 50%, 94%));
}

.modal-container {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  z-index: 10;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;

  background: hsla(0, 0%, 40%, .6);
}

.modal__btn.secondary--nav {
  color: #2196f3;
  background-color: #efefef;
}

/* using :target */
.modal-container:target {
  display: flex;
}

.modal {
  width: 50%;
  padding: 1rem;
  /* 4rem 2rem; */
  border-radius: .8rem;
  color: var(--light);
  background: var(--background);
  box-shadow: .4rem .4rem 2.4rem .2rem hsla(236, 50%, 50%, 0.3);
  position: relative;
  overflow: hidden;
}

.modal__title {
  font-size: 2rem;
}

.form__container {
  display: grid;
  justify-content: center;
}

.modal__description {
  margin-top: 1rem;
  font-size: 1.6rem;
  font-style: italic;
}

.modal__details {
  width: 100%;
  display: grid;
  margin-right: 4rem;
}

.modal__text {
  margin-bottom: 1rem;
}

.fileLabel {
  cursor: pointer;
  font-size: 1vw;
}

.uploadFile {
  display: none;
}

.flex-row-file {
  align-items: center;
  width: fit-content;
  margin-bottom: 1em;
}
.modal__btn.back--button{
  padding: 0.5rem 0.5rem;
  border: 1px solid #607d8b;
}
.lf-label-file {
  font-size: 3rem;
  display: contents;
  align-items: center;
  justify-content: center;
  background: #f5f6f8;
  cursor: pointer;
}

.modal__text::before {
  content: '';
  position: absolute;
  top: 0%;
  left: 100%;
  transform: translate(-50%, -50%);
  width: 18rem;
  height: 18rem;
  border: 1px solid hsla(0, 0%, 100%, .2);
  border-radius: 100rem;
  pointer-events: none;
}

.modal__btn {
  margin: auto;
  justify-content: center;
  justify-self: center;
  display: flex;
  padding: 1rem 1rem;
  border: 1px solid hsla(0, 0%, 100%, .4);
  border-radius: 100rem;
  color: inherit;
  background: transparent;
  font-size: 1.4rem;
  font-family: inherit;
  transition: .2s;
  cursor: pointer;
  max-width: -webkit-fill-available;
}

.flex--space--around {
  display: flex;
  justify-content: space-around;
}

.modal__btn:hover,
.modal__btn:focus {
  border-color: hsla(0, 0%, 100%, .6);
  transform: translateY(-.2rem);
}


.link-1 {
  font-size: 1.8rem;

  color: var(--light);
  background: var(--background);
  box-shadow: .4rem .4rem 2.4rem .2rem hsla(236, 50%, 50%, 0.3);
  border-radius: 100rem;
  padding: 1.4rem 3.2rem;

  transition: .2s;
}

.link-1:hover,
.link-1:focus {
  transform: translateY(-.2rem);
  box-shadow: 0 0 4.4rem .2rem hsla(236, 50%, 50%, 0.4);
}

.link-2 {
  width: 4rem;
  height: 4rem;
  border: 1px solid hsla(0, 0%, 100%, .4);
  border-radius: 100rem;
  background: none;
  color: inherit;
  font-size: 2.2rem;

  position: absolute;
  top: 2rem;
  right: 2rem;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: .2s;
}

.link-2::before {
  content: '×';

  transform: translateY(-.1rem);
}

.link-2:hover,
.link-2:focus {
  border-color: hsla(0, 0%, 100%, .6);
  transform: translateY(-.2rem);
}


.second-version-link,
.second-version-link:link {
  color: hsl(236, 50%, 50%);
  padding: .8rem 1.6rem .8rem .2rem;
  border-bottom: 2px solid hsl(236, 50%, 50%);

  font-size: 1.4rem;
  font-weight: bold;

  position: absolute;
  top: 4rem;
  right: 4rem;
}

.second-version-link::after {
  content: '\2197';

  position: absolute;
  top: 0;
  right: 0;

  font-size: .9em;
}

.abs-site-link {
  position: fixed;
  bottom: 20px;
  left: 20px;
  color: hsla(0, 0%, 0%, .6);
  font-size: 1.6rem;
}

@media screen and (max-width: 769px) {
  .modal-container {
    display: flex;

  }

  .modal__btn {
    margin-bottom: 1rem;
  }

  .modal {
    display: grid;
    padding: 5rem 0.5rem;
    width: 100%;
  }

  .link-2 {
    width: 2rem;
    height: 2rem;
    top: 1rem;
    right: 1rem;
  }

  .modal__text::before {
    transform: translate(-38%, -54%);
    width: 12rem;
    height: 12rem;
  }
}